import service from "@/utils/http"

export const get_search = (data: any) => {
    const refactor_data = {page_num: 1, page_size: 10, query: '', search_type: '', detail_type: ''}
    refactor_data.page_num = data.page_num
    refactor_data.page_size = data.page_size
    refactor_data.search_type = data.search_type
    refactor_data.detail_type = data.detail_type
    if(data.query == ''){
        refactor_data.query = 'null'
    }else{
        refactor_data.query = data.query
    }
    console.log(`/search/${refactor_data.detail_type}/${refactor_data.search_type}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`);
    return service.get(`/search/${refactor_data.detail_type}/${refactor_data.search_type}/${refactor_data.page_num}/${refactor_data.page_size}/${refactor_data.query}`)
}